.modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.44);
    opacity: 0;
    transition: opacity .15s linear;
}
.modal.active{
    opacity: 1;
}
.dialog{
    position: fixed;
    min-width: 85%;
    height: 85%;
    margin: 0 auto;
    top: 50%;
    left: 7%;
    border-radius: 6px;
    outline: 0;
    -webkit-box-shadow: 0 3px 9px rgba(0,0,0,.5);
    box-shadow: 0 3px 9px rgba(0,0,0,.5);
    transform: translate(0,-50%)!important;
    background-color: white;
}
.content{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}
.modal-haeder{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px;
}
.modal-haeder span{
    font-size: 18px;
    font-weight: 500;
    font-family: inherit;
}
.close-modal i{
    font-size: 21px;
    font-weight: 700;
    line-height: 1;
    color: #a19b9b;
    text-shadow: 0 1px 0 #fff;
}
.modal-item{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    flex-direction: column;
}
.photo-carusel{
    max-width: 550px;
    width: 100%;
    height: 550px;
    position: relative;
    overflow: hidden;
}
.phot-slid{
    width: 100%;
    transition: 0.3s all;
    height: 100%;
    position: absolute;
}
.phot-slid img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.dott-slider{
    margin-top: 15px;
}
.dott-slider .dott-img{
    display: inline-block;
    width: 80px;
    height: 80px;
    padding: 10px;
    text-indent: 0;
    border-radius: 0;
    margin: 0;
    border-bottom:  2px solid #fff;
}
.dott-slider .dott-img.active{
    border-bottom: 2px solid #3d97ee;
}
.dott-slider .dott-img img{
    width: 100%;
    height: auto;
}