.acess-slider {
    width: 100%;
    height: 400px;
    position: relative;
    overflow: hidden;
    margin: 20px auto;
}

.acess-slider .slid {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transform: translateX(-150px);
    transition: 0.4s all;
}

.acess-slider .slid.active {
    opacity: 1;
    transform: translateX(0);
}

.acess-slider .slid img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.product_title {
    padding: 15px;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.browser .broser-conteiner {
    display: flex;
}
.browser{
    display: flex;
    overflow: hidden;
}
.browser .broser-conteiner .wrapp {
   width: 350px;
   border: 1px solid rgb(236, 236, 236);
   transition: transform ease-in-out 0.2s;
   display: flex;
   justify-content: center;
   flex-direction: column;
   align-items: center;
   padding: 8px;
}
.browser .broser-conteiner .images{
    width: 100%;
    height: 300px;   
}
.browser .broser-conteiner .images img{
    width: 100%;
    height: 100%;
}
.btns{
    display: flex;
    justify-content: center;
    align-items: center;
}
.btns button{
    padding: 12px;
    color: black;
    border-radius: 5px;
    margin: 0 12px;
}
.proudct_title h1{
    font-family: "Poppins",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
}
.wrapp-item{
    width: 380px;
    margin: 0 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    flex-direction: column;
    position: relative;
}
.wrapp-item .view{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px;
    z-index: 1;
    top: 5px;
    opacity: 0;
    transform: translateY(50px);
    right: 20px;
    transition: 0.2s all;
}
.wrapp-item .view.active{
    transform: translateY(0);
    opacity: 1;
}
.wrapp-item .view > div{
    padding: 8px;
    background: #fff;
    border-radius: 50%;
    margin: 5px 0;
    cursor: pointer;
}
