.sub_header {
    background-color: rgba(247, 247, 247, 1);
    padding: 80px 0;
    position: relative;
    text-align: center;
    font-size: 50px;
    font-family: 'poppins', sans-serif;
    font-weight: 500;
    line-height: 50px;
    color: #222121;
    letter-spacing: 1px;
}

.category {
    display: flex;
    width: 100%;
    padding: 12px;
}

.sub_header h2 {
    margin-bottom: 15px;
}

.wrapp {
    width: 380px;
    margin: 0 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    position: relative;
    flex-direction: column;
}
.wrapp .view{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px;
    z-index: 1;
    top: 5px;
    opacity: 0;
    transform: translateY(50px);
    right: 20px;
    transition: 0.2s all;
}
.wrapp .view.active{
    transform: translateY(0);
    opacity: 1;
}
.wrapp .view > div{
    padding: 8px;
    background: #fff;
    border-radius: 50%;
    margin: 5px 0;
    cursor: pointer;
}

.main-sidbar {
    width: 25%;
    padding: 20px;
    box-shadow: 0 0 0.3px rgba(0, 0, 0, 0.2);
    background-color: rgb(255, 255, 255);
}