.cart {
    position: fixed;
    top: 0;
    right: -100%;
    background-color: #ffffff;
    width: 450px;
    transition: 0.3s all;
    height: 100%;
    z-index: 100000;
    transition: 0.3s;
    display: flex;
    flex-direction: column;

}
.cart.open{
    right: 0;
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 100;
}

.cart-header {
    padding: 20px;
    border-bottom: 1px solid rgba(0, 0, 0, .3);
    position: relative;
}

.times {
    color: rgba(0, 0, 0, .3);
    transition: all 0.3s ease-in-out;
    font-size: 25px;
    display: inline-block;
    position: absolute;
    top: 22px;
    left: 25px;
}

.times:hover {
    transform: rotate(360deg);
}

.cart-header h3 {
    text-align: center;
    color: #161922;
    margin-bottom: 15px;
    font-size: 30px;
    font-weight: 400;
}

.cart .cart-empty {
    display: flex;
    width: 100%;
    height: 80%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.cart .cart-empty {
    color: #626262;
    font-weight: 400;
    font-family: 'poppins', sans-serif;
    font-size: 17px;
}

.cart .cart-empty i {
    font-size: 30px;
}

.cart .cart-total {
    width: 100%;
    padding: 15px;
    background-color: #e3e3e3;
    font-family: 'poppins', sans-serif;
    color: #626262;
}

.cart .cart-total .subtotal {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #c8c8c8;
}
.cart-content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
    width: 100%;
}
.cart-content img{
    width: 100px;
    height: 100px;
}
.item-futer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    background-color: rgba(0,0,0,.01);
    border-top: 1px solid rgba(0,0,0,.04);
}
.item-futer .quantity{
    width: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    background-color: white;
    border: 1px solid #626262;
    padding: 4px 8px;
}
.item-futer .my-number{
    width: 50%;
    text-align: center;
    padding: 7px 15px;
}
.item-futer .my-number .quantity button{
    font-size: 25px;
}
.end-price{
    border-left: 1px solid #626262;
    padding: 6px;
}
.remove{
    color: #626262;
    font-style: italic;
    cursor: pointer;
}
.remove i{
    margin-right: 5px;
}
.remove span:hover{
    text-decoration: underline;
}