.restorans-carusel .carsuel-restoran .restoran-items .wrapper{
    width: 430px;
    transition: transform ease-in-out 0.5s;
}

.restorans-carusel .carsuel-restoran .restoran-items{
    display: flex;
}
.restorans-carusel{
    display: flex;
    position: relative;
    overflow: hidden;
}
.restorans-carusel button {
    position: absolute;
    width: 50px;
    height: 50px;
    text-align: center;
    background-color: white;
    border-radius: 50%;
    font-size: 17px;
    box-shadow: 0.2px 0.2px 3px #dedcdc;
    top: 40%;
}
.restorans-carusel .carsuel-restoran .restoran-items .wrapper span{
    font-size: 22px;
    font-weight: 500;
    font-family: 'poppins', sans-serif;
}
.restorans-carusel .left {
    left: 0;
    z-index: 1;
}

.restorans-carusel .right {
    right: 0;
}
