.title-label {
    color: #2e3331;
    font-size: 40px;
    line-height: 48px;
    font-weight: 600;
    letter-spacing: 0px;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.best-wrapp {
    width: 23%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.best-wrapp .images {
    margin-bottom: 15px;
}

.best-wrapp .title {
    color: #222322;
    font-size: 18px;
    line-height: 26px;
    font-weight: 500;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.best-wrapp .prices {
    font-size: 17px;
    color: #4c4e4d;
    font-weight: 500;
}

.pages {
    padding: 4px 6px;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    background-color: #f8f8f8;
    text-align: center;
}

.pages .page-number{
    color: #626262;
    margin: 0 8px;
    line-height: 35px;
    text-align: center;
    padding: 4px 10px;
    -webkit-border-radius: 5px;
    cursor: pointer;
    border-radius: 5px;
}
.pages .page-number.active{
    background-color: #0089F7;
    color: white;
}