.modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.361);
    transform: scale(0);
}

.modal.active {
    transform: scale(1);
}

.image {
    width: 100%;
    height: 100%;
    transition: 0.5s all;
}
 .image img{
    width: 100%;
    height: 100%;
}
.close{
    position: absolute;
    top: 15px;
    right: 20px;
    cursor: pointer;
    font-size: 30px;
    color: rgba(0,0,0,.3);
    transition: 0.3s all;
}
.close:hover{
    transform: rotate(360deg);
}
.variations{
    display: flex;
    align-items: center;
    margin-top: 15px;
}
.variations .quantity{
    width: 130px;
    position: relative;
    padding: 23px 35px;
    border: 1px solid #626262;
}
.variations .quantity button{
    font-size: 25px;
    font-weight: 600;
    border: none;
    outline: none;
}
