.carusel .carusel-items .wrapper {
    width: 438px;
    transition: transform ease-in-out 0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.carusel .carusel-items {
    display: flex;
}
.carusel .carusel-items .images{
    width: 100%;
    height: 300px;
}
.carusel .carusel-items .images img{
    width: 100%;
    height: 100%;
}
.slid-conteiner button {
    position: absolute;
    width: 50px;
    height: 50px;
    text-align: center;
    background-color: white;
    border-radius: 50%;
    font-size: 17px;
    box-shadow: 0.2px 0.2px 3px #dedcdc;
    top: 50%;
}

.slid-conteiner .left {
    left: 0;
    z-index: 1;
}

.slid-conteiner .right {
    right: 0;
}

.slid-conteiner {
    display: flex;
    position: relative;
    overflow: hidden;
}
