@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --background: #f2f2f2;
  --text-primary: #0f0f0f;
  --accent: #dfb017;
  --accent-hover: #cea315; 
  --shadow: 7px 15px 13px -4px #00000056;
}

[data-theme='dark'] {
  --background: #05010a;
  --white: #bababa;
  --index: 1000;
  --text-secondary: #ffffff;
  --accent: #6a5acd;
  --accent-hover: #5b4cbe; 
  --border: #bebebe;
  --shadow: 7px 15px 13px -4px #ffffff1b;
}

body {
  margin: 0;
  transition: all .5s ease-in-out;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: var(--background);
    color: var(--white);
}
  header .header-row{
  background-color: var(--background);
  color: var(--text-secondary);
}
.cart.open{
  background-color: var(--background);
}
.cart.open .times span{
  color: var(--border);
}
.cart.open .cart-tile{
  color: var(--border);
}
.cart.open .name-price{
  color: var(--accent);
}
header .wrapper nav ul li a{
  color: var(--text-secondary);
}
.acces-names{
  color: var(--white);
}
ul {
  list-style: none;
}

li,
a,
i,
button,
img {
  cursor: pointer;
}

a {
  text-decoration: none;
}
input{
  outline: none;
}